.case {
  background: #f7f7f7;
}
@media (max-width: 1199px) {
  .case {
    padding-bottom: 58px;
  }
}
@media (max-width: 767px) {
  .case {
    padding-bottom: 94px;
  }
}
.case img {
  max-width: 100%;
  height: auto;
}
.case__items {
  margin-top: 20px;
  margin-bottom: 46px;
}
@media (max-width: 1199px) {
  .case__items {
    margin-bottom: 26px;
  }
}
@media (max-width: 767px) {
  .case__items {
    margin-bottom: 49px;
