}
.case__item {
  background: #fff;
  margin-bottom: 35px;
}
@media (max-width: 1199px) {
  .case__item {
    margin-bottom: 20px;
  }
}
.case__item:after {
  content: '';
  display: table;
  clear: both;
}
.case__label {
  float: left;
  width: 217px;
  flex-shrink: 0;
  position: relative;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  height: 180px;
  border-bottom: 6px solid #006dfe;
  font-size: 30px;
  padding-bottom: 14px;
  margin-bottom: 22px;
}
@media (max-width: 1199px) {
  .case__label {
    height: 172px;
    margin-right: 34px;
  }
}
.case__label:after {
  content: '';
  position: absolute;
  width: 141px;
  height: 6px;
  background: #1d1d1d;
  bottom: -6px;
  left: 0;
  z-index: 1;
}
.case__icon {
  position: absolute;
  left: 89px;
  top: 67px;
  transform: translate(-50%, -50%);
  transition: all 0.2s ease-in;
}
.case__text {
  padding: 44px 30px 28px 258px;
  font-size: 18px;
  line-height: 2;
}
@media (max-width: 1199px) {
  .case__text {
    padding: 27px 25px 28px 20px;
  }
}
@media (max-width: 1199px) {
  .case__text {
    padding-bottom: 18px;
  }
}
.case__text p {
  margin-bottom: 1em;
}
.case__images {
  margin: 0 -6px;
  display: flex;
  flex-wrap: wrap;
}
.case__image {
  width: 33.333%;
  padding: 6px;
}
@media (max-width: 1199px) {
  .case__image {
    width: 50%;
  }
  .case__image:nth-child(n + 3) {
    display: none;
  }
}
@media (max-width: 767px) {
  .case__image {
    width: 100%;
  }
  .case__image:nth-child(n + 2) {
    display: none;
  }
}
.case__image img {
  max-width: 100%;
  height: auto;
}
.case__img {
  clear: both;
  height: 530px;
  background-position: 50% 50%;
  background-size: cover;
  transition: all 0.3s ease-in;
}
@media (max-width: 1199px) {
  .case__img {
    height: 248px;
  }
}
.case__block {
  padding: 62px 0 94px;
  position: relative;
  text-align: center;
}
@media (max-width: 1199px) {
  .case__block {
    padding: 45px 0 34px;
    margin: 0 -20px;
  }
}
.case__block:after {
  content: '';
  position: absolute;
  top: 100%;
  left: 50%;
  width: 460px;
  transform: translateX(-50%);
  height: 15px;
  background: #1d1d1d;
}
.case__header {
  font-size: 30px;
  font-weight: bold;
  line-height: 1.2;
  color: #006dfe;
  margin-bottom: 59px;
}
@media (max-width: 1199px) {
  .case__header {
    max-width: 600px;
    margin: 0 auto 40px;
  }
}
@media (max-width: 767px) {
  .case__header {
    font-size: 24px;
    line-height: 1.25;
    margin-bottom: 58px;
  }
}
.case__form {
  max-width: 770px;
  margin: 0 auto 38px;
}
@media (max-width: 1199px) {
  .case__form {
    max-width: 625px;
    margin-bottom: 53px;
  }
}
@media (max-width: 767px) {
  .case__form {
    margin: 0 20px 30px;
    max-width: none;
  }
}
.case__form .input {
  margin-bottom: 21px;
}
.case__form:before,
.case__form:after {
  content: '';
  position: absolute;
  top: 67px;
  bottom: 73px;
  width: 40px;
  border: 20px solid #006dfe;
}
@media (max-width: 767px) {
  .case__form:before,
  .case__form:after {
    display: none;
  }
}
.case__form:before {
  left: 0;
  border-right: 0;
}
.case__form:after {
  right: 0;
  border-left: 0;
}
